<template>
  <el-table
    :data="data"
    :default-sort="{prop: type+'.placement'}">
    <el-table-column label=" " :prop="type+'.placement'" min-width="35">
    </el-table-column>
    <el-table-column
      prop="name"
      label="战队"
      min-width="120"
    >
      <template slot-scope="scope">
        <a :href="'/owl/team/'+scope.row.id+'/'+scope.row.name" class="team-name">

          <img :src="scope.row.logo.main.svg" class="team-logo-standing" :alt="scope.row.name">
          <span>
                {{scope.row.name}}
              </span>
        </a>
      </template>
    </el-table-column>
    <el-table-column
      :label="meta.strings['owl.standings.header.won']"
      min-width="35">
      <template slot-scope="scope">
        {{scope.row[type].matchWin}}
      </template>
    </el-table-column>
    <el-table-column
      :label="meta.strings['owl.standings.header.lost']"
      min-width="35">
      <template slot-scope="scope">
        {{scope.row[type].matchLoss}}
      </template>
    </el-table-column>
    <el-table-column
      :label="meta.strings['owl.standings.header.matches-played']"
      min-width="45">
      <template slot-scope="scope">
        {{scope.row[type].matchWin+scope.row[type].matchLoss}}
      </template>
    </el-table-column>
    <el-table-column
      :label="meta.strings['owl.standings.header.map-record']"
      min-width="80">
      <template slot-scope="scope">
        {{`${scope.row[type].gameWin}-${scope.row[type].gameLoss}-${scope.row[type].gameTie}`}}
      </template>
    </el-table-column>
    <el-table-column
      :label="meta.strings['owl.standings.header.map-diff']"
      min-width="65">
      <template slot-scope="scope">
        <span style="color: red"
              v-if="scope.row[type].comparisons[1].value<0">{{scope.row[type].comparisons[1].value}}</span>
        <span style="color: green"
              v-else-if="scope.row[type].comparisons[1].value>0">+{{scope.row[type].comparisons[1].value}}</span>
        <span v-else>{{scope.row[type].comparisons[1].value}}</span>
      </template>
    </el-table-column>
  </el-table>

</template>

<script>
  import {getProp} from "@/common/util";

  export default {
    name: "StandingTable",
    props: ['data', 'type', 'meta'],
      mounted() {
      if (this.type !== 'league')
        this.data.forEach(x => {
          x[this.type] = getProp(this.type, x);
          x[this.type + '.placement'] = x[this.type].placement
        });
    },

    methods: {
      getPropFromStr(obj) {
        return getProp(this.type, obj)
      }
    }
  }
</script>

<style scoped>
  .team-name {
    display: flex;
    align-items: center;
  }

  .team-logo-standing {
    width: 1.6rem;
    height: 1.6rem;
    margin-right: .5rem;
  }
</style>



