<template>
  <div class="standings-container">
    <div v-loading="teams.length===0" style="min-height: 150px">
      <div class="standings" v-if="teams.length>0">

        <el-tabs type="border-card" value="stage" class="standings-out">
          <el-tab-pane :label="meta.strings['owl.standings.header.title.stage']" name="stage">
            <el-tabs type="border-card" value="stage1">
              <el-tab-pane :label="meta.strings['owl.schedule.stage.stage'+i]" :name="'stage'+i" v-for="i in [1,2,3,4]"
                           :key="i">
                <standing-table :data="teams" :type="'stages.stage'+i" :meta="meta"></standing-table>
              </el-tab-pane>

            </el-tabs>
          </el-tab-pane>
          <el-tab-pane :label="meta.strings['owl.standings.header.title.league']" name="league">
            <standing-table :data="teams" type="league" :meta="meta"></standing-table>
          </el-tab-pane>
        </el-tabs>

      </div>
    </div>
  </div>
</template>

<script>

  import StandingTable from "@/components/Teams/StandingTable";

  export default {
    name: "Teams",
    components: {StandingTable},
    data() {
      return {
        teams: [],
        meta: {},
        totalData: {},
      }
    },
    mounted() {
      this.fetchTeam()
    },
    methods: {
      fetchTeam() {
        this.teams = [];
        this.$owl.getAllTeams({season: this.season}).then(({team, meta}) => {
          this.totalData[this.season] = team;
          this.teams = team;
          this.meta = meta;
        })
      }
    },
    computed: {
      season() {
        return this.$store.getters.getSeason
      }
    },
    watch: {
      season: function (newSeason) {
        if (this.totalData[newSeason] === undefined)
          this.fetchTeam();
        else
          this.teams = this.totalData[this.season]
      }
    }

  }
</script>

<style scoped>
  .standings-container {
    text-align: left;
  }
</style>
<style>
  .standings .el-tabs__content {
    padding: 0 !important;
  }

  .standings-out > .el-tabs__content {
    padding-top: 10px !important;
  }
</style>



