<template>
  <div>
    <stage-switch v-model="isPlayoff"></stage-switch>
    <div v-loading="player===undefined" class="player-detail">
      <div v-if="player!==undefined">
        <div ref="header" class="player-header"
             :style="{backgroundColor:`${player.team.colors.primary.color}`,color:isBrightBg?'black':'white'}">
          <div style="display: flex;align-items: flex-end;">
            <div style="width: 700px;text-align: center">
              <img :src="player.basic.headshot" width="100%" class="player-img">
            </div>
            <div style=" padding:1rem">
              <div class="player-name">{{player.basic.name}}</div>
              <div class="player-name-real">{{player.basic.fullName}}</div>

              <div style="display: flex;align-items: flex-end;margin-top: 1rem">
                <img :src="$teamLogos[player.team.id]" class="player-team-logo">
              </div>

            </div>
          </div>
        </div>
        <el-tabs type="border-card" value="detail">
          <el-tab-pane label="英雄详情" name="detail">
            <el-table
              :data="player.heroes"
              :default-sort="{prop: 'time_played_total', order: 'descending'}">
              <el-table-column
                prop="hero"
                label="英雄"
                fixed
                sortable>
                <template slot-scope="scope">
                  <div class="hero-portrait-image-mask">
                    <div :style="`background-image: url(${getHeroIcon(scope.row.hero)})`"
                         class="hero-portrait-image"></div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column
                prop="eliminations_avg_per_10m"
                label="消灭"
                :formatter="toFixed2"
                sortable>
              </el-table-column>
              <el-table-column
                prop="deaths_avg_per_10m"
                label="阵亡"
                :formatter="toFixed2"
                sortable
              >
              </el-table-column>
              <el-table-column
                prop="hero_damage_avg_per_10m"
                label="伤害"
                :formatter="toFixed0"
                sortable>
              </el-table-column>
              <el-table-column
                prop="healing_avg_per_10m"
                label="治疗"
                :formatter="toFixed0"
                sortable>
              </el-table-column>
              <el-table-column
                prop="ultimates_earned_avg_per_10m"
                :formatter="toFixed2"
                label="终极技能"
                sortable
                width="120">
              </el-table-column>
              <el-table-column
                prop="final_blows_avg_per_10m"
                :formatter="toFixed2"
                label="最后一击"
                sortable
                width="120">
              </el-table-column>
              <el-table-column
                prop="time_played_total"
                label="游戏时间"
                :formatter="formatTime"
                sortable
                width="150">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="所有英雄" name="all">
            <el-table
              :data="player.all"
            >
              <el-table-column
                prop="type"
                label=" "
                fixed
              >
              </el-table-column>
              <el-table-column
                prop="eliminations_avg_per_10m"
                label="消灭"
                :formatter="toFixed2">
              </el-table-column>
              <el-table-column
                prop="deaths_avg_per_10m"
                label="阵亡"
                :formatter="toFixed2">
              </el-table-column>
              <el-table-column
                prop="hero_damage_avg_per_10m"
                label="伤害"
                :formatter="toFixed0"
              >
              </el-table-column>
              <el-table-column
                prop="healing_avg_per_10m"
                label="治疗"
                :formatter="toFixed0"
              >
              </el-table-column>
              <el-table-column
                prop="ultimates_earned_avg_per_10m"
                :formatter="toFixed2"
                label="终极技能"
                width="120">
              </el-table-column>
              <el-table-column
                prop="final_blows_avg_per_10m"
                :formatter="toFixed2"
                label="最后一击"
                width="120">
              </el-table-column>
              <el-table-column
                prop="time_played_total"
                label="游戏时间"
                :formatter="formatTime"
                width="150">
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>

      </div>
    </div>
  </div>
</template>

<script>
  import {convertSec, isBright, heroIcon} from "@/common/util"
  import StageSwitch from "@/components/StageSwitch";
  import OWL from "@/common/owl";

  export default {
    name: "Player",
    components: {StageSwitch},
    data() {
      return {
        player: undefined,
        isPlayoff: false,
        totalData: {2018: {}, 2019: {}}
      }
    },
    mounted() {
      this.fetchPlayer();
    },
    computed: {
      season() {
        return this.$store.getters.getSeason
      },
      seasonAndStage() {
        return [this.season, this.isPlayoff]
      },
      isBrightBg() {
        return this.player.team.colors.primary.color.slice(1)
      }
    },
    watch: {
      seasonAndStage: function () {
        if (this.totalData[this.season][this.isPlayoff] === undefined)
          this.fetchPlayer();
        else
          this.player = this.totalData[this.season][this.isPlayoff];

      }
    },
    methods: {
      fetchPlayer() {
        this.player = undefined;
        this.$owl.getPlayer(this.$route.params.id, {
          season: this.season,
          stage: this.isPlayoff ? OWL.STAGE.POST : OWL.STAGE.REGULAR
        }).then(player => {
            this.player = player;
            this.totalData[this.season][this.isPlayoff] = player;
          }
        )
      },
      toFixed0(row, column, cellValue) {
        return cellValue === null ? 'NA' : Number.isInteger(cellValue) ? cellValue : cellValue.toFixed(0)
      },
      toFixed2(row, column, cellValue) {
        return cellValue === null ? 'NA' : Number.isInteger(cellValue) ? cellValue : cellValue.toFixed(2)
      },
      formatTime(row, column, cellValue) {
        const [h, m] = convertSec(cellValue);

        return cellValue === null ? 'NA' : h === 0 ?
          m === 0 ? `${Math.round(cellValue)}秒` : `${m}分钟`
          : `${h}小时${m}分钟`
      },
      getHeroIcon(heroName) {
        return heroIcon(heroName)
      }
    },

  }
</script>

<style scoped>
  .player-detail {
    margin: 0 auto;
    text-align: left;
    min-height: 250px;
  }

  .player-header {
    color: black;
  }


  .player-img {
    max-width: 454px;
  }

  .player-team-logo {
    width: 10rem;
    height: 10rem;
  }

  .player-name {
    font-size: 2.5rem;
  }

  .player-name-real {
    font-size: 1rem;
  }

  .hero-portrait-image-mask {
    width: 60px;
    height: 72px;
    background-color: rgba(51, 51, 51, .9);
    background-clip: padding-box;
    overflow: hidden;
    border-radius: 7px;
  }

  .hero-portrait-image {
    display: inline-block;
    width: calc(100% + 10px);
    height: 100%;
    background-size: auto 100%;
    background-position: 50% 50%;
    position: relative;

  }

  @media screen and (max-width: 760px) {
    .hero-portrait-image-mask {
      width: 40px;
      height: 48px;
    }

    .player-team-logo {
      width: 7rem;
      height: 7rem;
    }

    .player-name {
      font-size: 2rem;
    }

    .player-name-real {
      font-size: 0.7rem;
    }

  }

</style>
<style>
  .player-detail .el-tabs--border-card > .el-tabs__content {
    padding: 0 10px
  }
</style>



